/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Éê„Éº */
.navbar {
  position: fixed;
  margin: auto;
  max-width: 1200px;
  background-color: #ffffff; /* ‚úÖ ÁôΩ„ÇíÂü∫Ë™ø„Å´ */
  z-index: 1100;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* „É°„Éã„É•„Éº„Ç¢„Ç§„Ç≥„É≥ */
.navbar .menu-toggle {
  position: fixed;
  top: 40px;
  left: 15px;
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.9); /* ‚úÖ ÂçäÈÄèÊòé„ÅßËªΩ„ÇÑ„Åã„Å´ */
  border-radius: 50%; /* ‚úÖ ‰∏∏„ÅÑ„Éú„Çø„É≥„Å´„Åô„Çã */
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15); /* ‚úÖ „Éï„É≠„Éº„ÉàÊÑü„ÇíÂá∫„ÅôÂΩ± */
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.2s ease;
  backdrop-filter: blur(8px); /* ‚úÖ ËÉåÊôØ„Çí„Åº„Åã„Åó„Å¶Â••Ë°å„Åç„ÇíÊºîÂá∫ */
}
/* „Éè„É≥„Éê„Éº„Ç¨„Éº„Ç¢„Ç§„Ç≥„É≥Ôºà‰∏âÊú¨Á∑öÔºâ */
.navbar .menu-icon {
  font-size: 26px;
  color: #333;
  transition: color 0.3s ease;
}
/* „Éõ„Éê„ÉºÊôÇÔºàÈùí„Å£„ÅΩ„ÅèÂÖâ„Çâ„Åõ„ÇãÔºâ */
.menu-toggle:hover {
  background: rgba(0, 123, 255, 0.2); /* ‚úÖ Èùí„Å£„ÅΩ„ÅÑÂÖâ */
  box-shadow: 0 6px 12px rgba(0, 0, 255, 0.3); /* ‚úÖ ÂΩ±„ÇíÂº∑Ë™ø */
  transform: scale(1.1); /* ‚úÖ „Åµ„Çè„Å£„Å®Êã°Â§ß */
}

/* „É°„Éã„É•„Éº„ÅåÈñã„ÅÑ„Åü„Å®„Åç„ÅÆ„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥ */
.menu-toggle.open {
  background: rgba(0, 123, 255, 0.3); /* ‚úÖ Èñã„ÅÑ„Åü„Å®„Åç„ÅÆËâ≤ */
  box-shadow: 0 6px 12px rgba(0, 0, 255, 0.4);
}

/* „Ç™„Éº„Éê„Éº„É¨„Ç§ */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 999;
  transition: opacity 0.3s ease;
}

/* „Çπ„É©„Ç§„Éâ„É°„Éã„É•„ÉºÔºà„Éï„É≠„Éº„ÉÜ„Ç£„É≥„Ç∞Ôºâ */
.menu {
  position: fixed;
  top: 10%;
  left: -300px; /* ÂàùÊúüÁä∂ÊÖã„ÅßÈö†„Åô */
  width: 260px;
  height: auto;
  max-height: 80%;
  background-color: rgba(255, 255, 255, 0.95); /* ‚úÖ Â∞ë„ÅóÈÄèÈÅé„Åï„Åõ„Çã */
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
  padding: 20px;
  border-radius: 12px; /* ‚úÖ Ëßí„Çí‰∏∏„Åè„Åó„Å¶ÊµÆ„ÅÑ„ÅüÂç∞Ë±°„Å´ */
  transition: left 0.3s ease, transform 0.3s ease;
  z-index: 1000;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* ‚úÖ ÊµÆ„ÅÑ„Å¶„ÅÑ„ÇãÂΩ± */
  backdrop-filter: blur(8px); /* ‚úÖ ËÉåÊôØ„Åº„Åã„Åó„Åß„Éï„É≠„Éº„ÉàÊÑü„ÇíÂº∑Ë™ø */
}

/* „É°„Éã„É•„Éº„ÇíÈñã„ÅÑ„Åü„Å®„Åç */
.menu.open {
  left: 30px;
}

/* Èñâ„Åò„Çã„Éú„Çø„É≥ */
.menu .close-button {
  background: none;
  color: black;
  font-size: 32px;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.menu .close-button:hover {
  color: #0056b3; /* ‚úÖ Èùí„Å£„ÅΩ„ÅèÂ§âÂåñ */
  transform: scale(1.1); /* ‚úÖ „Åµ„Çè„Å£„Å®Êã°Â§ß */
}

/* „É°„Éã„É•„ÉºÈ†ÖÁõÆ */
.menu li {
  width: 100%;
  padding: 12px 20px;
  list-style: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); /* ‚úÖ ËñÑ„ÅÑÁ∑ö„ÅßÂå∫Âàá„Çã */
  text-align: left; /* ‚úÖ Â∑¶ÂØÑ„Åõ */
}

.menu li:last-child {
  border-bottom: none; /* ÊúÄÂæå„ÅÆ„É°„Éã„É•„Éº„Å´„ÅØ„Éú„Éº„ÉÄ„Éº„Å™„Åó */
}

/* „É°„Éã„É•„Éº„É™„É≥„ÇØ */
.menu li a,
.menu li button {
  width: 100%;
  display: block;
  color: #333;
  font-size: 18px;
  text-decoration: none;
  padding: 10px 0;
  transition: background-color 0.3s ease, color 0.3s ease;
  text-align: left;
  font-weight: bold;
  transition: all 0.2s ease-in-out; /* üîÑ Êªë„Çâ„Åã„Å™„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥ */
}

.menu li a:hover,
.menu li button:hover {
  color: #007bff; /* ‚úÖ „Éõ„Éê„ÉºÊôÇ„Å´Èùí„Åè */
  transform: scale(1.1); /* ‚úÖ „Åµ„Çè„Å£„Å®Êã°Â§ß */
}

/* „É≠„Ç∞„Ç¢„Ç¶„Éà„Éú„Çø„É≥ */
.logout-button {
  background: none;
  border: none;
  color: #333;
  font-size: 18px;
  cursor: pointer;
  width: 100%;
  text-align: left;
}

.logout-button:hover {
  background-color: rgba(0, 123, 255, 0.1);
  color: #007bff;
}

/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Éê„Éº„ÅÆ„É¨„Çπ„Éù„É≥„Ç∑„ÉñÂØæÂøú */
@media (max-width: 768px) {
  .navbar {
    width: 100%;
    max-width: none;
    padding: 0 15px;
  }
  
  .navbar .menu-toggle {
    top: 20px;
    left: 10px;
    width: 40px;
    height: 40px;
  }
  
  .navbar .menu-icon {
    font-size: 22px;
  }
  
  .menu {
    top: 15%;
    left: -100%; /* ÁîªÈù¢Â§ñ„Å´Èö†„Åô */
    width: 80%;
    max-width: 250px;
    padding: 15px;
    max-height: 80vh; /* ÁîªÈù¢„ÅÆÈ´ò„Åï„ÅÆ80%„Åæ„Åß */
    overflow-y: auto; /* „Çπ„ÇØ„É≠„Éº„É´ÂèØËÉΩ„Å´„Åô„Çã */
  }
  
  .menu.open {
    left: 10px;
  }
  
  .menu .close-button {
    top: 15px;
    right: 15px;
    font-size: 28px;
  }
  
  .menu li {
    padding: 10px 15px;
  }
  
  .menu li a,
  .menu li button {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .navbar .menu-toggle {
    top: 15px;
    left: 5px;
    width: 35px;
    height: 35px;
  }
  
  .navbar .menu-icon {
    font-size: 20px;
  }
  
  .menu {
    top: 10%;
    width: 90%;
    max-width: 220px;
    padding: 10px;
    max-height: 80vh; /* ÁîªÈù¢„ÅÆÈ´ò„Åï„ÅÆ80%„Åæ„Åß */
    overflow-y: auto; /* „Çπ„ÇØ„É≠„Éº„É´ÂèØËÉΩ„Å´„Åô„Çã */
  }
  
  .menu.open {
    left: 5px;
  }
  
  .menu .close-button {
    top: 10px;
    right: 10px;
    font-size: 26px;
  }
  
  .menu li {
    padding: 8px 12px;
  }
  
  .menu li a,
  .menu li button {
    font-size: 14px;
  }
}

