}

/* ÁôΩ„ÅÑ‰∏∏„ÅÆ„Çπ„Ç§„ÉÉ„ÉÅÈÉ®ÂàÜ */
.toggle-circle {
  width: 21px;
  height: 21px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  left: 2px;
  transition: transform 0.3s ease-in-out;
}

/* ON „ÅÆÊôÇ„ÅÆ„Çπ„Ç§„ÉÉ„ÉÅ‰ΩçÁΩÆ */
.toggle-switch.active .toggle-circle {
  width: 21px;
  height: 21px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  left: 2px;
  transition: transform 0.3s ease-in-out;
}

