}

/* ÈÄöÁü•„ÅÆ„Éê„ÉÉ„Ç∏ÔºàÊú™Ë™≠‰ª∂Êï∞Ôºâ */
.badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background: red;
  color: white;
  font-size: 12px;
  padding: 3px 7px;
  border-radius: 50%;
}

/* ÈÄöÁü•„Éù„ÉÉ„Éó„Ç¢„ÉÉ„Éó */
.notification-popup {
  position: absolute;
  width: 320px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
